export default function DataTable({ columns, data, onEdit, onDelete }) {
    return (
      <div className="bg-white shadow-md rounded-lg overflow-hidden mt-6">
        <table className="w-full border-collapse">
          <thead className="sticky top-0 bg-gray-800 text-white">
            <tr>
              {columns.map((col) => (
                <th key={col.key} className="p-4 text-left">
                  {col.label}
                </th>
              ))}
              <th className="p-4 text-center">Editar</th>
              <th className="p-4 text-center">Excluir</th>
            </tr>
          </thead>
          <tbody>
            {data.map((item, index) => (
              <tr
                key={item.codigo}
                className={`border-t ${index % 2 === 0 ? "bg-gray-100" : "bg-white"}`}
              >
                {columns.map((col) => (
                  <td key={col.key} className="p-4">
                    {col.render ? col.render(item) : item[col.key]}
                  </td>
                ))}
                <td className="p-4 text-center">
                  <button
                    onClick={() => onEdit(item)}
                    className="bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition-all"
                  >
                    ‚úèÔ∏è Editar
                  </button>
                </td>
                <td className="p-4 text-center">
                  <button
                    onClick={() => onDelete(item)}
                    className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-all"
                  >
                    üóëÔ∏è Excluir
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    );
  }
  